body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html, body,#root, .App{
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h2, h3{
  padding: 0;
  margin: 0;
}

/* BASE CLASSES */
.centerAll{
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
}
.flexColumnCenter{
  /* vertical flex center and avoid some flex side effects */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.margin0Auto{
  margin: 0 auto;
}
.marginAuto{
  margin: auto;
}
.noUserSelect{
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.fillContainer{
  width: 100%;
  height: 100%;
}
.textCenter{
  text-align: center;
}
.textLeft{
  text-align: left;
}
.mainHover:hover{
  color: blue;
}
.pointer{
  cursor: pointer;
}
.noMargin{
  margin: 0;
}

.textUpper{
  text-transform: uppercase;
}

.overflowHidden{
  overflow: hidden;
}
/* END BASE CLASSES */




.topNav{
  height: 2.8rem;
  z-index: 3;
  position: fixed;
  top: 0;
  width: 100%;
  overflow: hidden;
  background-color: rgb(240, 240, 240);
  padding: 0;
  /* background-color: rgba(252, 2, 252, 1); */
}
.navList{
  height: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  max-width: 60rem;
  padding: 0 !important;
}
.navList>.somePadding, .userInfo>.somePadding{
  list-style: none;
  padding: 0.2rem 1rem 0.4rem 1rem;
  cursor: pointer;
}

.navList>.somePadding:hover,.userInfo>.somePadding:hover{
  background-color: rgba(200,200,200,0.4)
}

.gamesContainer{
  padding-top: 2.8rem;
}

.userInfo{
  margin-left: auto;
}


.gradientBackground {
  background-image: -webkit-linear-gradient(blue, violet);
  background-image: linear-gradient(blue, violet);
  background-size: cover;
}

.fullPageContainer{
  min-height: 100%;
  display: flex;
  flex-direction: column;
}



