@-webkit-keyframes shake {
    0% { -webkit-transform: translate(2px, 0); transform: translate(2px, 0); }
    25% { -webkit-transform: translate(-2px, 0); transform: translate(-2px, 0); }
    50% { -webkit-transform: translate(2px, 0); transform: translate(2px, 0); }
    75% { -webkit-transform: translate(-2px, 0); transform: translate(-2px, 0); }
    100% { -webkit-transform: translate(2px, 0); transform: translate(2px, 0); }
  }

@keyframes shake {
    0% { -webkit-transform: translate(2px, 0); transform: translate(2px, 0); }
    25% { -webkit-transform: translate(-2px, 0); transform: translate(-2px, 0); }
    50% { -webkit-transform: translate(2px, 0); transform: translate(2px, 0); }
    75% { -webkit-transform: translate(-2px, 0); transform: translate(-2px, 0); }
    100% { -webkit-transform: translate(2px, 0); transform: translate(2px, 0); }
  }

